	<view class="ai-container">
		<!-- È°∂ÈÉ®ÂØºËà™Ê†è -->
		<view class="nav-bar">
			<view class="nav-left" @tap="goBack">
				<text class="back-icon">‚Üê</text>
				<text class="back-text">ËøîÂõû</text>
			</view>
			<view class="nav-title">AIÊô∫ËÉΩÂä©Êâã</view>
			<view class="nav-right"></view>
		</view>
		
		<view class="content">
			<!-- È°∂ÈÉ®‰ªãÁªç -->
			<view class="intro-section">
				<view class="intro-card">
					<view class="intro-icon">ü§ñ</view>
					<text class="intro-title">AIÊô∫ËÉΩÂä©Êâã</text>
					<text class="intro-desc">‰∏∫‰Ω†Êèê‰æõÊÉÖÁª™ÁÆ°ÁêÜÂª∫ËÆÆÂíåÂøÉÁêÜÂÅ•Â∫∑ÊåáÂØº</text>
				</view>
			</view>
			
			<!-- Âø´ÈÄüÈóÆÈ¢ò -->
			<view class="quick-questions">
				<view class="section-title">Â∏∏ËßÅÈóÆÈ¢ò</view>
				<view class="question-tags">
					<view 
						v-for="question in quickQuestions" 
						:key="question.id"
						class="question-tag"
						@tap="askQuestion(question.text)"
					>
						{{ question.text }}
					</view>
				</view>
			</view>
			
			<!-- ÂØπËØùÂå∫Âüü -->
			<view class="chat-section">
				<view class="section-title">Êô∫ËÉΩÂØπËØù</view>
				<view class="chat-container">
					<scroll-view class="chat-messages" scroll-y scroll-into-view="msg-bottom">
						<view 
							v-for="(message, index) in chatMessages" 
							:key="index"
							class="message-item"
							:class="message.type"
						>
							<view class="message-avatar">
								<text v-if="message.type === 'user'">üë§</text>
								<text v-else>ü§ñ</text>
							</view>
							<view class="message-content">
								<text class="message-text">{{ message.content }}</text>
								<text class="message-time">{{ formatTime(message.time) }}</text>
							</view>
						</view>
						<view id="msg-bottom"></view>
					</scroll-view>
					
					<view class="chat-input">
						<input 
							class="input-field" 
							v-model="inputMessage" 
							placeholder="ËæìÂÖ•‰Ω†ÁöÑÈóÆÈ¢ò..."
							@confirm="sendMessage"
						/>
						<button class="send-btn" @tap="sendMessage" :disabled="!inputMessage.trim()">
							ÂèëÈÄÅ
						</button>
					</view>
				</view>
			</view>
			
			<!-- Êó•ËÆ∞ÂàÜÊûê -->
			<view class="diary-analysis">
				<view class="section-title">Êó•ËÆ∞Êô∫ËÉΩÂàÜÊûê</view>
				<view class="analysis-card">
					<text class="analysis-desc">Âü∫‰∫é‰Ω†ÁöÑÊó•ËÆ∞ÂÜÖÂÆπÔºåAI‰ºö‰∏∫‰Ω†Êèê‰æõ‰∏™ÊÄßÂåñÁöÑÊÉÖÁª™Âª∫ËÆÆ</text>
					<button class="analysis-btn" @tap="analyzeDiary">ÂàÜÊûê‰ªäÊó•Êó•ËÆ∞</button>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
import { aiApi } from '../../utils/api.js'

export default {
	data() {
		return {
			inputMessage: '',
			chatMessages: [],
			quickQuestions: []
		}
	},
	
	onLoad() {
		this.initChat()
		this.loadCommonQuestions()
	},
	
	methods: {
		// ËøîÂõû‰∏ä‰∏ÄÈ°µ
		goBack() {
			uni.navigateBack({
				delta: 1
			})
		},
		
		async loadCommonQuestions() {
			try {
				const response = await aiApi.getCommonQuestions()
				if (response && response.code === 200) {
					this.quickQuestions = response.data || []
				}
			} catch (error) {
				console.error('Âä†ËΩΩÂ∏∏ËßÅÈóÆÈ¢òÂ§±Ë¥•:', error)
			}
		},
		initChat() {
			// Ê∑ªÂä†Ê¨¢ËøéÊ∂àÊÅØ
			this.addMessage('assistant', '‰Ω†Â•ΩÔºÅÊàëÊòØ‰Ω†ÁöÑAIÊÉÖÁª™Âä©ÊâãÔºåÊúâ‰ªÄ‰πàÂèØ‰ª•Â∏ÆÂä©‰Ω†ÁöÑÂêóÔºü')
		},
		
		askQuestion(question) {
			this.inputMessage = question
			this.sendMessage()
		},
		
		async sendMessage() {
			if (!this.inputMessage.trim()) return
			
			const message = this.inputMessage.trim()
			this.addMessage('user', message)
			this.inputMessage = ''
			
			// ÊòæÁ§∫AIÊ≠£Âú®ËæìÂÖ•
			this.addMessage('assistant', 'Ê≠£Âú®ÊÄùËÄÉ‰∏≠...', true)
			
			try {
				const res = await aiApi.chat({
					message: message,
					timestamp: Date.now()
				})
				
				console.log('AI API ÂìçÂ∫î:', res)
				
				// ÁßªÈô§"Ê≠£Âú®ËæìÂÖ•"Ê∂àÊÅØ
				this.chatMessages.pop()
				
				if (res && res.code === 200) {
					// Â∞ùËØïÂ§öÁßçÂèØËÉΩÁöÑÂìçÂ∫îÊ†ºÂºè
					const reply = res.data?.reply || res.data?.message || res.data?.content || res.data?.answer || res.message || 'Êä±Ê≠âÔºåÊàëÁé∞Âú®Êó†Ê≥ïÂõûÁ≠îËøô‰∏™ÈóÆÈ¢ò„ÄÇ'
					this.addMessage('assistant', reply)
				} else if (res && res.data) {
					// Â¶ÇÊûúÂêéÁ´ØÁõ¥Êé•ËøîÂõûÊï∞ÊçÆÂØπË±°
					const reply = res.data.reply || res.data.message || res.data.content || res.data.answer || 'Êä±Ê≠âÔºåÊàëÁé∞Âú®Êó†Ê≥ïÂõûÁ≠îËøô‰∏™ÈóÆÈ¢ò„ÄÇ'
					this.addMessage('assistant', reply)
				} else {
					console.error('AI API ËøîÂõûÊ†ºÂºèÂºÇÂ∏∏:', res)
					this.addMessage('assistant', `Êä±Ê≠âÔºåÊàëÈÅáÂà∞‰∫Ü‰∏Ä‰∫õÈóÆÈ¢ò„ÄÇ${res?.message || 'ËØ∑Á®çÂêéÂÜçËØï'}`)
				}
			} catch (error) {
				console.error('AI API Ë∞ÉÁî®Â§±Ë¥•:', error)
				this.chatMessages.pop()
				const errorMsg = error.message || error.errMsg || 'ÁΩëÁªúËøûÊé•Âá∫Áé∞ÈóÆÈ¢òÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúÂêéÈáçËØï„ÄÇ'
				this.addMessage('assistant', `ÁΩëÁªúËøûÊé•Âá∫Áé∞ÈóÆÈ¢òÔºö${errorMsg}`)
			}
		},
		
		addMessage(type, content, isTyping = false) {
			this.chatMessages.push({
				type,
				content,
				time: new Date(),
				isTyping
			})
			
			// ÊªöÂä®Âà∞Â∫ïÈÉ®
			setTimeout(() => {
				uni.pageScrollTo({
					scrollTop: 9999,
					duration: 300
				})
			}, 100)
		},
		
		async analyzeDiary() {
			try {
				uni.showLoading({ title: 'ÂàÜÊûê‰∏≠...' })
				
				const res = await aiApi.diaryAnalysis({
					action: 'analyze_today',
					timestamp: Date.now()
				})
				
				uni.hideLoading()
				
				if (res.code === 200) {
					this.addMessage('assistant', res.data.analysis || 'Âü∫‰∫é‰Ω†ÁöÑÊó•ËÆ∞ÂÜÖÂÆπÔºåÊàë‰∏∫‰Ω†Êèê‰æõ‰∫Ü‰∏Ä‰∫õÂª∫ËÆÆ„ÄÇ')
				} else {
					uni.showToast({
						title: res.message || 'ÂàÜÊûêÂ§±Ë¥•',
						icon: 'none'
					})
				}
			} catch (error) {
				uni.hideLoading()
				uni.showToast({
					title: 'ÂàÜÊûêÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï',
					icon: 'none'
				})
			}
		},
		
		formatTime(date) {
			const now = new Date()
			const diff = now - date
			
			if (diff < 60000) return 'ÂàöÂàö'
			if (diff < 3600000) return `${Math.floor(diff / 60000)}ÂàÜÈíüÂâç`
			if (diff < 86400000) return `${Math.floor(diff / 3600000)}Â∞èÊó∂Ââç`
			
			return date.toLocaleDateString()
		}
	}
}
</script>

